<template>
  <div class="container__nearby">
    <div class="container__nearby__title">附近商铺</div>
    <!-- 使用v-for渲染 -->
    <router-link :to="`/shop/${item.id}`" v-for="item of nearbyItem" :key="item.id"
      ><ShopInfo :item="item" :showButtom="true"> </ShopInfo
    ></router-link>
  </div>
  <Toast :message="toastMessage" v-if="isShow" ></Toast>
</template>
<script>
import { ref } from 'vue'
import { get } from '../../utils/request'
import Toast, { showToastEffect } from '../../components/Toast.vue'
import ShopInfo from '../../components/ShopInfo.vue'
// 定义接口获得附近商店数据
const userNearbyEffect = (showToast) => {
  const nearbyItem = ref([])
  const getNearbyItem = async () => {
    try {
      const result = await get('/api/home/nearby')
      if (result.data.code === '0000') {
        nearbyItem.value = result.data.data
        console.log(result)
      } else {
        showToast('数据为空')
      }
    } catch (error) {
      showToast('获取数据失败！')
    }
  }
  getNearbyItem()
  return { nearbyItem }
}
export default {
  name: 'Nearby',
  components: { Toast, ShopInfo },
  setup () {
    const { isShow, toastMessage, showToast } = showToastEffect()
    const { nearbyItem } = userNearbyEffect(showToast)
    return { nearbyItem, isShow, toastMessage }
  }
}
</script>
<style lang="scss" scoped>
@import '../../style/mixins.scss';
@import '../../style/base.scss';
.container__nearby {
  padding: 16rem 18rem;
  &__title {
    font-family: $pfm;
    font-size: 18rem;
    color: #333333;
    margin-bottom: 14rem;
  }
}
</style>
