<template>
    <div class="docker">
        <!-- 使用列表渲染 -->
        <span @click="dockerItemClick(item.path)" v-for='item of dockerItems' :key='item.icon' :class="{docker__item:true ,docker__item_active:item.tag===tag}">
            <span class="docker__item__iconfont iconfont" v-html="item.icon">  </span>
            <span class="docker__item__text"> {{item.text}} </span>
        </span>
    </div>
</template>

<script>
import { useRouter } from 'vue-router'
export default {
  name: 'Docker',
  props: ['tag'],
  setup () {
    const router = useRouter()
    const dockerItems = [
      { icon: '&#xe692;', text: '首页', tag: 'home', path: '/' },
      { icon: '&#xe600;', text: '购物车', tag: 'cart', path: '/' },
      { icon: '&#xe645;', text: '订单', tag: 'order', path: '/order-list' },
      { icon: '&#xe8a0;', text: '我的', tag: 'user', path: '/' }
    ]
    const dockerItemClick = (path) => {
      router.push({ path: path })
    }
    return { dockerItems, dockerItemClick }
  }
}

</script>
<style lang="scss" scoped>
.docker{
    position:fixed;
    bottom: 0;
    left: 0;
    background: #FFFFFF;
    box-shadow: 0 -1px 1px 0 #F1F1F1;
    width: 375rem;
    height: 49rem;
    align-items: center;
    justify-content: space-around;
    padding-top: 10rem;
    display: flex;
    &__item{
        &_active{
            .docker__item__text{
                color: #1fa4fc;
            }
            color: #1fa4fc;
        }
        display: flex;
        flex-direction: column;
        align-items: center;
        &:hover{
            color: #1fa4fc;
        }
        &__iconfont{
            font-size: 20rem;
        }
        &__text{
            color: #333333;
            margin-top: 2rem;
            font-size: 20rem;
            transform: scale(0.5,0.5);
            transform-origin: center top;
            font-family: PingFangSC-Regular;
        }
    }
}
</style>
